/****************************************************************************** 
 *createRouter : 라우터 생성을 도와주는 함수
 *path, component 부분만 수정하면 나만의 페이지들을 만들어 낼 수 있음
 *vue-router 만든사람이 정한 사용법일 뿐이라 굳이 이해할 필요 없음
 ******************************************************************************/ 

import { createWebHashHistory, createRouter } from "vue-router"; //라이브러리명 적으면 라이브러리 import해서 쓰는것일뿐
import List from './components/List.vue';
import Home from './components/Home.vue';
import Detail from './components/Detail.vue'
import Author from './components/Author.vue'
import Comment from './components/Comment.vue'

const routes = [
   {
     path: "/detail/:id",
     component: Detail,
     children : [
         {
             path : 'author',
             component : Author
         },
         {
             path : 'comment',
             component : Comment
         },
     ]
   },
   {
     path: "/list",
     component: List,
   },
   {
     path: "/",
     component: Home,
   },
//    {
//      path: "/:anything(.*)",
//      component: Home,
//    },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

//여러개의 route에 같은 navigation guard 추가
//router.beforeEach((to, from) => {
    //페이지 변경 전에 실행할 코드
//})

export default router; 